.container {
  max-width: 600px;
  margin: 50px auto; /* centers the container horizontally */
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* optional: adds a light shadow */
  background-color: #fff; /* optional: adds a background */
  border-radius: 8px; /* optional: rounded corners */
}
.title {
  text-align: center;
  color: #333;
}

.form {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.input {
  flex: 1;
  padding: 0.5rem;
  font-size: 1rem;
  margin-right: 0.5rem;
}

.button {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  margin-left: 0.25rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.button.delete {
  background-color: #dc3545;
}

.button.active {
  background-color: #28a745;
}

.task-list {
  list-style: none;
  padding: 0;
}

.task {
  background: #f9f9f9;
  padding: 0.75rem;
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 4px;
}

.task.completed {
  text-decoration: line-through;
  color: gray;
}

.filter {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}